.header {
  position: absolute;
  top: 1.75em;
  left: 0;
  width: 100%;
  background: rgba(48, 52, 54, 0.9);
  &__inner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    padding-left: 17em;
  }
  &__logo {
    display: block;
    width: 15.25em;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    img {
      width: 100%;
    }
  }
  &__location {
    display: flex;
    align-items: center;
    &:hover {
      span {
        color: #43b64b;
      }
    }
    i {
      flex-shrink: 0;
      width: 1.5625em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #ffffff;
      margin-left: 0.625em;
      transition: all 0.5s;
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    margin-left: 3.75em;
    &:hover {
      span {
        color: #43b64b;
      }
    }
    i {
      flex-shrink: 0;
      width: 1.5625em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #ffffff;
      margin-left: 0.625em;
      transition: all 0.5s;
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    margin-left: 3.75em;
    &:hover {
      span {
        color: #43b64b;
      }
    }
    i {
      flex-shrink: 0;
      width: 1.5625em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.3125em;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      margin-left: 0.625em;
      transition: all 0.5s;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 3.75em;
  }
  &__btn {
    height: 3.5em;
    border: 0.0625em solid #43B64B;
    padding: 0 3.125em;
    &:hover {
      background: #43B64B;
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  &__search {
    width: 3.5em;
    height: 3.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 0.0625em solid #43B64B;
    padding: 0;
    margin-left: 1.25em;
    img {
      max-width: 1.5625em;
    }
    &:hover {
      background: #43b64b;
    }
  }
  &__list {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    li {
      position: relative;
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -6.0625em;
          transform: translate(0, -50%);
          width: 2.5em;
          height: 0.0625em;
          background: rgba(189, 189, 189, 0.5);;
        }
      }
    }
  }
  &__link {
    font-size: 1.0625em;
    line-height: 120%;
    color: #ffffff;
    &:hover {
      color: #43b64b;
    }
  }
  &__item {
    &--active {
      .header__item-show {
        span {
          color: #43b64b;
        }
      }
      .header__item-hidden {
        display: block;
      }
    }
    &-show {
      cursor: pointer;
      span {
        font-size: 1.0625em;
        line-height: 120%;
        color: #ffffff;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 19.9375em;
      padding-top: 1em;
      display: none;
    }
    &-content {
      background: #303436;
      padding: 1.25em 1.5625em 1.5625em 1.5625em;
    }
    &-link {
      display: block;
      font-size: 1.1875em;
      line-height: 120%;
      color: #ffffff;
      padding-bottom: 0.625em;
      position: relative;
      &:hover {
        color: #43B64B;
      }
      &:not(:last-child) {
        margin-bottom: 0.625em;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 1.875em;
          height: 0.0625em;
          opacity: 0.5;
          background: #676B6C;
        }
      }
    }
  }
  &__burger {
    width: 2em;
    height: 1.125em;
    position: relative;
    z-index: 10;
    margin-left: 1.875em;
    cursor: pointer;
    display: none;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 3.5em;
      height: 3.5em;
      border: 0.0625em solid #43B64B;
    }
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 0.0625em;
      background: #ffffff;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
    &--active {
      span {
        &:nth-child(1) {
          top: 0.5em;
          transform: rotate(-45deg);
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          bottom: 0.5625em;
          transform: rotate(45deg);
        }
      }
    }
  }
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.125em 0;
}
.header-bottom {
  border-top: 0.0625em solid #676B6C;
  padding: 1.0625em 0;
  position: relative;
}

@media screen and (max-width: 992px) {
  .header {
    .container {
      padding: 0 0.9375em;
    }
    &__inner {
      padding-left: 12.8125em;
    }
    &__logo {
      width: 11.625em;
    }
    &__location {
      display: none;
    }
    &__mail {
      margin-left: 0;
    }
    &__tel {
      margin-left: 1.5em;
    }
    &__btns {
      margin-left: 1.5em;
    }
    &__btn {
      padding: 0 2.5em;
    }
    &__list {
      li {
        &:not(:last-child) {
          &:after {
            right: -2.5em;
            width: 1.5em;
          }
        }
      }
    }
    &__link {
      font-size: 0.9375em;
    }
    &__item {
      &-show {
        span {
          font-size: 0.9375em;
        }
      }
      &-link {
        font-size: 0.9375em;
      }
    }
  }
  .header-top {
    padding: 0.5em 0;
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__inner {
      padding:0 0.75em 0 9em;
    }
    &__logo {
      width: 7.5em;
    }
    &__mail {
      display: none;
    }
    &__tel {
      margin-left: 0;
    }
    &__btns {
      margin-left: 1.25em;
    }
    &__btn {
      padding: 0 2.1875em;
      span {
        font-size: 0.9375em;
      }
    }
    &__search {
      margin-left: 1.25em;
    }
    &__burger {
      display: block;
    }
    &__list {
      flex-direction: column;
      justify-content: center;
      li {
        &:not(:last-child) {
          margin-bottom: 1.25em;
          &:after {
            display: none;
          }
        }
      }
    }
    &__item {
      &-show {
        text-align: center;
      }
      &-hidden {
        position: static;
        min-width: 100%;
        padding-top: 0;
      }
      &-content {
        background: transparent;
        padding: 1.25em 0 0 0;
        text-align: center;
      }
      &-link {
        padding: 0;
        &:not(:last-child) {
          &:after {
            display: none;
          }
        }
      }
    }
  }
  .header-top {
    padding: 1em 0;
  }
  .header-bottom {
    position: fixed;
    z-index: 9;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    background: #303436;
    border: unset;
    padding: 9.375em 2em 4em 2em;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    &::-webkit-scrollbar {
      width: 0.1875em;
    }
    &::-webkit-scrollbar-track {
      background: #303436;
      border-radius: 0.1875em;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 0.1875em;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      padding-left: 6em;
    }
    &__logo {
      width: 5.625em;
    }
    &__tel {
      i {
        width: 1.1875em;
      }
      span {
        font-size: 0.9375em;
        margin-left: 0.1875em;
      }
    }
    &__btns {
      margin-left: 0.625em;
    }
    &__btn {
      padding: 0 0.625em;
    }
    &__search {
      margin-left: 0.625em;
    }
    &__burger {
      margin-left: 1.25em;
    }
  }
  .header-top {
    padding: 0.4375em 0;
  }
}

@media screen and (max-width: 450px) {
  .header {
    &__tel {
      display: none;
    }
    &__btns {
      margin-left: 0;
    }
    &__btn {
      height: 2.875em;
    }
    &__search {
      width: 2.875em;
      height: 2.875em;
      margin-left: 0.5em;
    }
    &__burger {
      &:after {
        width: 2.875em;
        height: 2.875em;
      }
    }
  }
}