*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  box-sizing: border-box;
}

body,
div,
p,
h1,
h2,
h3,
h4,
h5,
ul,
li,
a,
form,
label {
  margin: 0;
  padding: 0;
  border: 0;
  text-decoration: none;
  line-height: 1;
  box-sizing: border-box;
}

button,
input,
textarea {
  font-family: 'Montserrat';
  outline: none !important;
}

button {
  font-size: 1em;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
}

input[type=text]:focus,
input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus,
input:active {
  outline: none !important;
}

html {
  height: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
  font-size: 1600px;
  @media screen and (max-width: 1600px) {
    font-size: 100vw;
  }
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  height: 100%;
  overflow-x: hidden;
  font-size: 1%;
  @media screen and (max-width: 992px) {
    font-size: 1.612903%;
  }
  @media screen and (max-width: 768px) {
    font-size: 2.083333%;
  }
  @media screen and (max-width: 576px) {
    font-size: 2.777777%;
  }
  @media screen and (max-width: 450px) {
    font-size: 3.555555%;
  }
}
.fixed-body {
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
img {
  margin: 0;
  padding: 0;
  height: auto;
  object-fit: cover;
}

img {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
  transition: 0.5s;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin: 0 auto;
  padding: 0 3.125em;
  width: 100%;
  max-width: 1600px;
  @media screen and (max-width: 992px) {
    padding: 0 1.25em;
  }
}